@mixin font-font(
  $size: 1.8rem,
  $weight: 400,
  $family: $main-font,
  $style: normal
) {
  font: {
    family: $family;
    style: $style;
    weight: $weight;
    size: $size;
  }
}

// // font family menu
@mixin main-font {
  @include font-font(1.6rem, 500, $main-font, normal);
  line-height: 22px;
}
@mixin font-btn {
  @include font-font(2rem, 700, $font-primary, normal);
  line-height: 22px;
}
@mixin font-phone {
  @include font-font(2rem, 700, $font-primary, normal);
  line-height: 22px;
}
@mixin font-phone-hover {
  @include font-font(2.4rem, 700, $font-primary, normal);
  line-height: 27px;
}
@mixin font-header-title {
  @include font-font(6.4rem, 700, $font-primary, normal);
  line-height: 71px;
}
@mixin font-header-text {
  @include font-font(2.4rem, 700, $font-primary, normal);
  line-height: 27px;
}
@mixin font-header-decor-text {
  @include font-font(13rem, 700, $font-primary, normal);
  line-height: 145px;
}
@mixin font-header-decor-text-3 {
  @include font-font(3.2rem, 300, $main-font, italic);
  line-height: 39px;
}
@mixin font-info-top-text {
  @include font-font(1.8rem, 400, $main-font, italic);
  line-height: 22px;
}
@mixin font-h3-title {
  @include font-font(6rem, 400, $font-primary, normal);
  line-height: 67px;
}
@mixin font-info-title {
  @include font-font(1.8rem, 700, $main-font, normal);
  line-height: 22px;
}

@mixin font-info-text {
  @include font-font(1.8rem, 400, $main-font, normal);
  line-height: 22px;
}

@mixin font-btn-price {
  @include font-font(4rem, 700, $main-font, normal);
  line-height: 45px;
}

@mixin font-icon-price {
  @include font-font(3.5rem, 700, $main-font, normal);
  line-height: 42px;
}

@mixin font-tech-list {
  @include font-font(2rem, 700, $main-font, normal);
  line-height: 24px;
}

@mixin font-life {
  @include font-font(3.2rem, 800, $main-font, italic);
  line-height: 39px;
}

