/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box;
  font-size: 10px;
  scroll-behavior: smooth;
}

.body {
  /*   background-image: url(../assets/home.png);
  background-attachment: fixed, fixed;
